import React from 'react';
import styled from 'styled-components';
import Skillbar from '../../components/Skillbar';
import Card from '../../components/Card';

const Caption = styled.h4`
  font-size: 3rem;
  font-weight: 800;

  @media screen and (max-width: 767px) {
    font-size: 2rem;
  }

  media screen and (max-width: 479px) {
    font-size: 28px;
    line-height: 32px;
  }
`;

const SkillbarWrapper = styled.div`
  margin-top: 1.4rem;
`;
const StyledSkillbar = styled(Skillbar)`
  margin-top: 12px;
  color: rgba(255, 255, 255, 1);
  box-shadow: 0 5px 15px rgba(145, 145, 145, 0.5);
`;
const Divider = styled.div`
  margin: 20px 0;
`;

const skills = [
  {
    name: 'JavaScript',
    score: 93
  },
  {
    name: 'Node',
    score: 90
  },
  {
    name: 'React',
    score: 90
  },
  {
    name: 'Angular',
    score: 90
  },
  {
    name: 'MongoDB',
    score: 90
  },
  {
    name: 'AWS',
    score: 80
  }
];

function Skills() {
  return (
    <Card>
      <Caption>Skills</Caption>
      <Divider className="stripe pink" />
      <SkillbarWrapper>
        {skills.map((skill) => {
          return (
            <StyledSkillbar
              key={skill.name}
              name={skill.name}
              score={skill.score}
            />
          );
        })}
      </SkillbarWrapper>
    </Card>
  );
}

export default Skills;
